import {SubmitHandler, useForm} from "react-hook-form";
import React, {SetStateAction, useState} from "react";
import {useMediaQuery} from "react-responsive";
import cn from 'classnames';
import axiosInstance from "../../api/axiosInstans";
import {Agree} from "./agree";


interface Param {
    dName ?: string;
    dCell ?: string;
    dJumin ?: string;
    carType ?: string;
    businessType ?: string;
    carNum ?: string;
    carUse ?: string;
    collectionYn ?: string;
    provisionYn ?: string;
    inquiryYn ?: string;
    marketingYn ?: string;
    sharingYn ?: string;
}
interface Props {
    setIsOpen : React.Dispatch<SetStateAction<boolean>>
}
export default function RegisterForm({setIsOpen} : Props) {
    const isMobile = useMediaQuery({maxWidth : 930})
    const [agreeOpen, setAgreeOpen] = useState(false);
    const [type, setType] = useState('');
    const isMobi = /Mobi/i.test(window.navigator.userAgent);
    const {
        register,
        watch,
        handleSubmit,
        formState: { errors },
    } = useForm<Param>({
        defaultValues: {
            dName : '',
            dCell : '',
            dJumin : '',
            carNum : '',
            businessType :'',
            carType : '',
            carUse : '',
            collectionYn : '',
            provisionYn : '',
            inquiryYn : '',
            marketingYn : '',
            sharingYn : '',
        },
        mode: 'onChange',
    });
    const onSubmit : SubmitHandler<Param> = async (data : Param) => {
        try{
            const res = await axiosInstance.post('/api1001', data, {
            });
            axiosInstance.post('/count', {job : 'submit'}).then(r => console.log('count'));

            // @ts-ignore
            if(res.code === 200){
                // @ts-ignore
                alert(res.msg);
            }else {
                // @ts-ignore
                alert(res.msg);
            }

            setIsOpen(false);
        }catch(e){
            alert(' . \n  . \n 1670-0470');
        }
    }
    const onError = (errors : any) => {
        console.log(errors)
    }
    const car = watch("carUse");
    return (
        <div className={cn('w-full flex flex-col', {'text-[12px] overflow-y-scroll pt-[60px] h-full' : isMobi})}>
            <form id={'regi'} className='w-[calc(100% - 20%)] px-8 my-8 space-y-4 base_font relative'
                  onSubmit={handleSubmit(onSubmit, onError)}
            >
                <div className='w-full flex'>
                    <label className='basis-1/3 flex_center'>
                        
                    </label>
                    <input
                        type={'text'}
                        className={cn('basis-2/3',{'mobi_input' : isMobile, 'input' : !isMobile})}
                        {...register('dName', {
                            required : true
                        })}
                    />
                </div>
                <div className='w-full flex'>
                    <label className='basis-1/3 flex_center'>
                        
                    </label>
                    <input type={'text'}
                           className={cn('basis-2/3',{'mobi_input' : isMobile, 'input' : !isMobile})}
                           {...register('dJumin', {
                               required : true
                           })}
                    />
                </div>
                <div className='w-full flex'>
                    <label className='basis-1/3 flex_center'>
                         
                    </label>
                    <input type={'text'}
                           className={cn('basis-2/3',{'mobi_input' : isMobile, 'input' : !isMobile})}
                           {...register('dCell', {
                               required : true
                           })}
                    />
                </div>
                <div className='w-full flex'>
                    <label className='basis-1/3 flex_center'>
                         / 
                    </label>
                    <select className={cn('basis-2/3',{'mobi_select' : isMobile, 'select' : !isMobile})}
                            defaultValue={'00'}
                            {...register('businessType', {
                                required : true
                            })}
                    >
                        <option value={'00'} disabled={true} ></option>
                        <option></option>
                        <option></option>
                    </select>
                </div>
                <div className='w-full flex'>
                    <label className='basis-1/3 flex_center'>
                         
                    </label>
                    <select className={cn('basis-2/3',{'mobi_select' : isMobile, 'select' : !isMobile})}
                            //onChange={(e: React.ChangeEvent<HTMLSelectElement>) => setType(e.currentTarget.value)}
                            {...register('carUse', {

                                required : true,
                            })}
                            defaultValue={'00'}>
                        <option value={'00'} disabled={true}></option>
                        <option value={'01'}></option>
                        <option value={'02'}></option>
                    </select>
                    {errors.carUse && <p> .</p>}
                </div>
                <div className='w-full flex'>
                    <label className='basis-1/3 flex_center'>
                        
                    </label>
                    {
                        car === '01' ?
                            <select className={cn('basis-2/3',{'mobi_select' : isMobile, 'select' : !isMobile})}
                                    {...register('carType', {
                                        required : false
                                    })}
                                    defaultValue={'00'}>
                                <option value={'00'} disabled={true}></option>
                                <option>(DE) EV</option>
                                <option> 5 EV ()</option>
                            </select>
                            :
                            <select className={cn('basis-2/3',{'mobi_select' : isMobile, 'select' : !isMobile})}
                                    {...register('carType', {
                                        required : false
                                    })}
                                    disabled={car === '' && true}>
                                <option></option>
                                <option> EV</option>
                                <option> EV</option>
                            </select>
                    }
                </div>
                <div className='w-full flex'>
                    <label className='basis-1/3 flex_center'>
                         
                    </label>
                    <input type={'text'}
                           className={cn('basis-2/3',{'mobi_input' : isMobile, 'input' : !isMobile})}
                           {...register('carNum', {
                               required : true
                           })}
                    />
                </div>
            </form>
            <div className={cn('bg-white w-full ',{'absolute bottom-0' : !isMobile})}>
                <div className={cn('flex flex-col base_font p-4 mt-2 w-full space-y-1',{'text-[10px]' : isMobile, 'text-[14px]' : !isMobile})}>
                    <div className='flex '>
                        <input type='checkbox'

                        />
                        <p className='ml-2'>     .</p>
                    </div>
                    <div className='flex '>
                        <input type='checkbox'
                               {...register('collectionYn', {
                                   required : true
                               })}
                        />
                        <p className='ml-2'>() ,    ()</p>
                        <button className='ml-auto' onClick={(e :React.MouseEvent<HTMLButtonElement>) => {
                            setAgreeOpen(true);
                            setType('collect')
                        }}>[]</button>
                    </div>
                    <div className='flex '>
                        <input type='checkbox'
                               {...register('inquiryYn', {
                                   required : true
                               })}
                        />
                        <p className='ml-2'>()    ()</p>
                        <button className='ml-auto' onClick={(e :React.MouseEvent<HTMLButtonElement>) => {
                            setAgreeOpen(true);
                            setType('inquiry')
                        }}>[]</button>
                    </div>
                    <div className='flex '>
                        <input type='checkbox'
                               {...register('provisionYn', {
                                   required : true
                               })}
                        />
                        <p className='ml-2'>()    ()</p>
                        <button className='ml-auto' onClick={(e :React.MouseEvent<HTMLButtonElement>) => {
                            setAgreeOpen(true);
                            setType('provision')
                        }}
                        >[]</button>
                    </div>
                    <div className='flex '>
                        <input type='checkbox'
                               {...register('sharingYn', {
                                   required : true
                               })}
                        />
                        <p className='ml-2'>      ()</p>
                        <button className='ml-auto' onClick={(e :React.MouseEvent<HTMLButtonElement>) => {
                            setAgreeOpen(true);
                            setType('sharing');
                        }}
                        >[]</button>
                    </div>
                    <div className='flex'>
                        <input type='checkbox'
                               {...register('marketingYn', {
                               })}
                        />
                        <p className='ml-2'>    </p>
                        <button className='ml-auto' onClick={(e :React.MouseEvent<HTMLButtonElement>) => {
                            setAgreeOpen(true);
                            setType('marketing');
                        }}>[]</button>
                    </div>
                </div>
                <div className='w-full flex_center my-4'>
                    <button type={'submit'} form={'regi'} className='border w-[120px] h-[40px] rounded-xl bg_base text-white font-bold'></button>
                </div>
            </div>
            {
                agreeOpen && <Agree type={type} agreeOpen={agreeOpen} setAgreeOpen={setAgreeOpen}/>
            }
        </div>
    )
}

